<script>

    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';
    import axios from 'axios';
    import moment from 'moment';

    export default {
      //

      data() {

        return {
            loading: true,
            users: [],
            menu: [],
        };

      },
      methods: {
      
        async loadFilter()
        {
            NProgress.start();

            try {
              
                const response = await axios.get('auth/user',);

                this.users = response.data.user;
                this.menu = response.data.menu.menu.menu;
                this.loading = false;


            } 
            catch (error) {

                this.loading = false;
                this.$notify({
                    type: 'error',
                    title: "Echec de recuperation"
                });
            }

            NProgress.done();

        },

        async UpdateUser()
        {
            NProgress.start();

            try {

              this.users.date_naiss = moment(new Date(this.users.date_naiss)).startOf('day').format('YYYY-MM-DD');
              const response = await axios.post('v1/xperf/setting/user/editProfile', this.users);
              this.$notify({
                    type: 'success',
                    title: "Notification",
                    message: "Votre profil a été mis à jour avec succès"
                });
            } 
            catch (error) {
                this.$notify({
                    type: 'error',
                    title: "Echec de recuperation"
                });
            }

            NProgress.done();

        },

      },

      created () {
        this.loadFilter();
      }

    }
</script>
